






































































import { Component, Vue, Watch } from 'vue-property-decorator';
import { TreeStructureProperty } from '@/interface/treeStructureProperty';
import { NodeProperty } from '@/interface/nodeProperty';
import { PathProperty } from '@/interface/pathProperty';
import { NodeStatusDto } from '@/interface/nodeStatusDto';
import Axios from 'axios';
import * as d3 from 'd3';
import { mapGetters } from 'vuex';
import { NodeStatusModel } from '@/store/modules/nodeStatus';
import { TreeStructureResponse } from '@/interface/treeStructureResponse';
import { findChildrenByIDOrName } from '@/util/tree.util';

@Component({
  computed: {
    ...mapGetters({
      nodeStatus: 'nodeStatus',
      tree: 'tree',
    }),
  },
})
export default class Graph extends Vue {
  /**
   * Computed property
   */
  get svgDimension(): { width: number; height: number } {
    return {
      width: this.containerWidth,
      height: this.containerHeight,
    };
  }

  get nodes(): NodeProperty[] {
    return this.treeDescendants.map((o) => {
      const nodeProperty: NodeProperty = {
        id: o.data.id,
        class: 'node',
        translation: `translate(${o.y}, ${o.x})`,
        text: o.data.name || '',
        x: 0,
      };

      nodeProperty.class += ` node-${o.data.name ? 'active' : 'inactive'}`;
      nodeProperty.class += ` node-${
        o.data.status === 'offline' ? 'down' : 'up'
      }`;

      if (o.children) {
        nodeProperty.x = -13;
        nodeProperty.class += ' node-text-before';
      } else {
        nodeProperty.x = 13;
        nodeProperty.class += ' node-text-after';
      }

      return nodeProperty;
    });
  }

  get paths(): PathProperty[] {
    return this.pathDescendants.map((o) => {
      const pathProperty: PathProperty = {
        diagonal: '',
        class: 'link',
      };
      pathProperty.diagonal = `M ${o.target.y} ${o.target.x} C ${(o.target.y +
        o.source.y) /
        2} ${o.target.x}, ${(o.target.y + o.source.y) / 2} ${o.source.x}, ${
        o.source.y
      } ${o.source.x}`;

      pathProperty.class += ` link-${
        o.target.data.status === 'offline' ? 'down' : 'up'
      }`;

      return pathProperty;
    });
  }

  private get containerWidth(): number {
    return this.treeStructure
      ? (this.treeStructure.height + 1) * this.nodeMargin
      : 0;
  }

  private get containerHeight(): number {
    return this.treeStructure ? this.treeStructure.leaves().length * 30 : 0;
  }

  private get contentWidth(): number {
    return this.treeStructure ? this.treeStructure.height * this.nodeMargin : 0;
  }

  private get contentHeight(): number {
    return this.treeStructure ? this.treeStructure.leaves().length * 30 : 0;
  }

  private get visibleData(): TreeStructureProperty | null {
    if (this.rawData) {
      const clone = JSON.parse(JSON.stringify(this.rawData));
      if (clone.children) {
        const children = this.hideData(clone.children);
        clone.children = children;
      }
      return clone;
    } else {
      return null;
    }
  }

  private get treeStructure(): d3.HierarchyNode<TreeStructureProperty> | null {
    return this.visibleData
      ? d3.hierarchy<TreeStructureProperty>(this.visibleData)
      : null;
  }

  private get treeDescendants(): Array<
    d3.HierarchyPointNode<TreeStructureProperty>
  > {
    return this.treeStructure
      ? d3
          .tree<TreeStructureProperty>()
          .size([this.contentHeight, this.contentWidth - 200])(
            this.treeStructure,
          )
          .descendants()
      : [];
  }

  private get pathDescendants(): Array<
    d3.HierarchyPointLink<TreeStructureProperty>
  > {
    return this.treeStructure
      ? d3
          .tree<TreeStructureProperty>()
          .size([this.contentHeight, this.contentWidth - 200])(
            this.treeStructure,
          )
          .links()
      : [];
  }
  public rawData: TreeStructureProperty | null = null;
  public nodeStatus!: NodeStatusDto;
  public tree!: TreeStructureResponse;
  private readonly nodeMargin = 400;

  /**
   * Life cycle hook
   */
  public created() {
    if (this.tree) {
      this.updateRawData(this.tree);
    }
    if (this.nodeStatus) {
      this.updateNodeStatus(this.nodeStatus);
    }
  }

  /**
   * Functions
   */
  public toggle(nodeProperty: NodeProperty) {
    if (nodeProperty.id) {
      const node = this.findNodeByIDOrName(nodeProperty.id);
      if (node && node.children) {
        node.children.forEach((child) =>
          this.$set(child, 'hidden', !child.hidden),
        );
      }
    }
  }

  @Watch('nodeStatus')
  private nodeStatusWatcher(newValue: NodeStatusDto, oldValue: NodeStatusDto) {
    this.updateNodeStatus(newValue);
  }

  private updateNodeStatus(nodeStatus: NodeStatusDto) {
    if (this.rawData) {
      this.setStatusProperty(this.rawData, 'online');
      nodeStatus.L1.forEach((id) => {
        const node = this.findNodeByIDOrName(id);
        if (node) {
          this.setStatusProperty(node, 'offline');
        }
      });
      nodeStatus.L2.forEach((id) => {
        const node = this.findNodeByIDOrName(id);
        if (node) {
          this.setStatusProperty(node, 'offline');
        }
      });
      nodeStatus.circuit.forEach((circuit) => {
        const node = this.findNodeByIDOrName(circuit.CIRCUIT);
        if (node) {
          this.setStatusProperty(node, circuit.STATUS);
        }
      });
    }
  }

  @Watch('tree')
  private treeWatcher(
    newValue: TreeStructureResponse,
    oldValue: TreeStructureResponse,
  ) {
    this.updateRawData(newValue);
    if (this.nodeStatus) {
      this.updateNodeStatus(this.nodeStatus);
    }
  }

  private updateRawData(newValue: TreeStructureResponse) {
    this.rawData = newValue as TreeStructureProperty;
    this.setHiddenProperty(this.rawData, false);
    this.setStatusProperty(this.rawData, 'online');
  }

  private setStatusProperty(
    treeProperty: TreeStructureProperty,
    isOnline: 'online' | 'offline',
  ) {
    this.$set(treeProperty, 'status', isOnline);
    if (treeProperty.children) {
      treeProperty.children.forEach((child) =>
        this.setStatusProperty(child, isOnline),
      );
    }
  }

  private setHiddenProperty(
    treeProperty: TreeStructureProperty,
    isHidden = false,
  ) {
    this.$set(treeProperty, 'hidden', isHidden);
    if (treeProperty.children) {
      treeProperty.children.forEach((o) => this.setHiddenProperty(o, isHidden));
    }
  }

  private findNodeByIDOrName(idOrName: string): TreeStructureProperty | null {
    return findChildrenByIDOrName(
      this.rawData,
      idOrName,
    ) as TreeStructureProperty;
  }

  private hideData(data: TreeStructureProperty[]): TreeStructureProperty[] {
    const result = data.filter((o) => {
      if (o.children) {
        o.children = this.hideData(o.children);
      }
      return !o.hidden;
    });
    return result;
  }
}
