


























































import { Component, Vue, Watch } from "vue-property-decorator";
import { mapGetters } from "vuex";
import { NodeStatusDto } from "@/interface/nodeStatusDto";

interface OfflineCircuitInfo {
  l1: string;
  l2: string;
  circuit: string;
}

@Component({
  computed: {
    ...mapGetters({
      nodeStatus: "nodeStatus"
    })
  }
})
export default class FaultTable extends Vue {
  public nodeStatus!: NodeStatusDto;
  public offlineCircuits: OfflineCircuitInfo[] = [];
  public offlineL1: string[] = [];
  public offlineL2: string[] = [];

  public created() {
    if (this.nodeStatus) {
      this.updateNodeStatus(this.nodeStatus);
    }
  }

  @Watch("nodeStatus")
  private nodeStatusWatcher(newValue: NodeStatusDto, oldValue: NodeStatusDto) {
    this.updateNodeStatus(newValue);
  }

  private updateNodeStatus(status: NodeStatusDto) {
    this.offlineL1 = status.L1;
    this.offlineL2 = status.L2;
    this.offlineCircuits = status.circuit
      .filter(o => o.STATUS === "offline")
      .sort((a, b) => (a.L1 > b.L1 ? 1 : -1))
      .sort((a, b) => (a.L2 > b.L2 ? 1 : -1))
      .map(o => ({
        l1: o.L1,
        l2: o.L2,
        circuit: o.CIRCUIT
      }));
  }

  get offlineL1Circuit(): OfflineCircuitInfo[] {
    return this.offlineCircuits.filter(
      o => this.offlineL1.indexOf(o.l1) !== -1
    );
  }

  get offlineL2Circuit(): OfflineCircuitInfo[] {
    return this.offlineCircuits.filter(
      o => this.offlineL2.indexOf(o.l2) !== -1
    );
  }

  get independentOfflineCircuit(): OfflineCircuitInfo[] {
    return this.offlineCircuits.filter(o => (this.offlineL1.indexOf(o.l1) === -1 && this.offlineL2.indexOf(o.l2) === -1))
  }
}
